<template>
  <div class="content">
    <div class="table" id="printDiv" >
      <table border="1">
        <caption>清远市粤足绿色建材投资有限公司</caption>
        <caption>货款结算单</caption>
        <tbody>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td>合同编号:</td>
            <td colspan="2" align="center">QYYS ZXKS 2023-003</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="exportBtn" @click="exportExcel">导出</div>
    <div class="exportBtn" v-print="printContent">打印</div>
  </div>
</template>

<script>
  import { Row } from 'element-ui';
import ExcelJS from 'exceljs';
  import FileSaver from "file-saver";
  import Vue from 'vue';
  import Print from 'vue-print-nb'
  Vue.use(Print)
  
  
  export default {
    data() {
      return {
        printContent: {
          id: 'printDiv',
          // preview: true, // 预览工具是否启用
          // previewTitle: '储能服务费结算单', // 预览页面的标题
          // popTitle: '储能服务费结算单', // 打印页面的页眉
          extraCss: "https://cdn.bootcdn.net/ajax/libs/animate.css/4.1.1/animate.compat.css, https://cdn.bootcdn.net/ajax/libs/hover.css/2.3.1/css/hover-min.css",
          extraHead: '<meta http-equiv="Content-Language"content="zh-cn"/>',
          previewBeforeOpenCallback() {
            console.log('正在加载预览窗口')
          },
          previewOpenCallback() {
            console.log('已经加载完预览窗口')
          },
          beforeOpenCallback(vue) {
            vue.printLoading = true
            console.log('打开之前')
          },
          openCallback(vue) {
            vue.printLoading = false
            console.log('执行了打印')
          },
          closeCallback() {
            console.log('关闭了打印工具')
          },
          clickMounted(vue) {
            console.log('点击了打印按钮')
            // vue.printSet.popTitle = vue.tableHead // 动态设置页眉
          },
        },
        headData: [{
            cellName: "A1",
            mergeName: "A1:G1",
            value: "清远市粤足绿色建材投资有限公司",
            style: {
              vertical: "middle",
              horizontal: "center",
            },

          },
          {
            cellName: "A2",
            mergeName: "A2:G2",
            value: "货款结算单",
            style: {
              vertical: "middle",
              horizontal: "center",
            }
          },
          {
            cellName: "E5",
            mergeName: "",
            value: "合同编号:",
            style: null,
          },
          {
            cellName: "F5",
            mergeName: "F5:G5",
            value: "QYYS ZXKS 2023-003",
            style: {
              vertical: "middle",
              horizontal: "center",
            },
          },
          {
            cellName: "A7",
            mergeName: "A7:B7",
            value: "购货单位:",
            style: null,
          },
          {
            cellName: "C7",
            mergeName: "C7:G7",
            value: "佛山市三水区乐平镇东兴新型建材厂",
            style: null,
          },
          {
            cellName: "A9",
            mergeName: "A9:B9",
            value: "结算周期:",
            style: null,
          },
          {
            cellName: "C9",
            mergeName: "C9:G9",
            value: "2023年 12 月 21日 至 2023年 12 月 21日",
            style: null,
          }
        ],
        listData: [
          [1, "剥离风化土", 2150, 18, "1%", 38713.86, 383.31],
          [2, "剥离风化土", 2150, 18, "1%", 38713.86, 383.31]
        ]
      }
    },
    methods: {
      exportExcel() {
        const excel_name = "test.xlsx";
        // 创建工作簿
        const wb = new ExcelJS.Workbook();
        // 添加工作表
        const sheet = wb.addWorksheet("sheet");
        this.setTableContent(sheet);
        wb.xlsx.writeBuffer().then((buffer) => {
          let file = new Blob([buffer], {
            type: "application/octet-stream",
          });
          FileSaver.saveAs(file, "ExcelJS.xlsx");
        });
      },
      // 设置表格内容
      setTableContent(sheet) {
        sheet.columns = [{
            width: 10
          },
          {
            width: 20
          },
          {
            width: 10
          },
          {
            width: 20
          },
          {
            width: 15
          },
          {
            width: 20
          },
          {
            width: 20
          },
        ];
        for (var i = 0; i < this.headData.length; i++) {
          this.createHeadCell(this.headData[i], sheet);
        }
        sheet.addRow([]);
        sheet.addRow(['序号', '类别', '数量', '含税单价(元)', '税率', '含税金额(元)', '税额(元)']);
        sheet.getRow(11).eachCell((cell, rowIdx) => {
          cell.border = {
            top: {
              style: 'medium',
            },
            left: {
              style: 'medium',
            },
            bottom: {
              style: 'medium',
            },
            right: {
              style: 'medium',
            }
          }
          cell.font = {
            size: 16,
            bold: true
          }
          cell.alignment  = {
            vertical: "middle",
            horizontal: "center",
          }
        });
        sheet.addTable({
          ref: 'A12',
          headerRow: false,
          totalsRow: false,
          columns: [
            {name: '序号'},
            {name: '类别'},
            {name: '数量'},
            {name: '含税单价(元)'},
            {name: '税率'},
            {name: '含税金额(元)', totalsRowFunction: 'sum'},
            {name: '税额(元)', totalsRowFunction: 'sum'},
          ],
          rows: this.listData,
        });
        sheet.addRow(['合计', null, null, null, null, 38713.86, 38713.86]);
        sheet.addRow(['不含税结算总金额:', null, 95785.4, null, null, null, null]);
        sheet.addRow(['税额:', null, 957.85, null, null, null, null]);
        sheet.addRow(['含税结算总金额:', null, 96743.26, null, null, null, null]);
        // 按开始行，开始列，结束行，结束列合并（相当于 K10:M12）
        sheet.mergeCells(12 + this.listData.length,1,12 + this.listData.length,5);
        for (var i = 0; i < this.listData.length; i++) {
          this.setRowStyle(12 + i, sheet);
        }
        this.setRowStyle(12 + this.listData.length, sheet);
        for (var i = 0; i < 3; i++) {
          sheet.mergeCells(13 + i + this.listData.length,1,13 + i + this.listData.length,2);
          sheet.mergeCells(13 + i + this.listData.length,3,13 + i + this.listData.length,7);
          this.setBottomRowStyle(13 + i + this.listData.length, sheet);
        }
        sheet.addRow();
        sheet.addRow();
        sheet.addRow(['采购方盖章:', null, null, null, '销售方盖章:', null, null]);
        sheet.mergeCells(sheet._rows.length,1,sheet._rows.length,2);
        sheet.mergeCells(sheet._rows.length,5,sheet._rows.length,6);
        sheet.getRow(sheet._rows.length).eachCell((cell, rowIdx) => {
          cell.font = {
            size: 14,
            bold: false
          }
          cell.alignment  = {
            vertical: "middle",
            horizontal: "center",
          }
        });
        sheet.addRow();
        sheet.addRow();
        sheet.addRow(['采购方审核:', null, '销售方销售审核:', null, '销售方财务审核:', null, null]);
        sheet.mergeCells(sheet._rows.length,1,sheet._rows.length,2);
        sheet.mergeCells(sheet._rows.length,3,sheet._rows.length,4);
        sheet.mergeCells(sheet._rows.length,5,sheet._rows.length,6);
        sheet.getRow(sheet._rows.length).eachCell((cell, rowIdx) => {
          cell.font = {
            size: 14,
            bold: false
          }
          cell.alignment  = {
            vertical: "middle",
            horizontal: "center",
          }
        });
        sheet.addRow();
        sheet.addRow();
        sheet.addRow(['年　月　日', null, '年　月　日', null, '年　月　日', null, null]);
        sheet.getRow(sheet._rows.length).eachCell((cell, rowIdx) => {
          cell.font = {
            size: 14,
            bold: false
          }
          cell.alignment  = {
            vertical: "middle",
            horizontal: "center",
          }
        });
        sheet.mergeCells(sheet._rows.length,1,sheet._rows.length,2);
        sheet.mergeCells(sheet._rows.length,3,sheet._rows.length,4);
        sheet.mergeCells(sheet._rows.length,5,sheet._rows.length,7);
      },
      //
      createHeadCell(data, sheet) {
        const titleCell = sheet.getCell(data.cellName);
        if (data.mergeName) {
          sheet.mergeCells(data.mergeName);
        }
        if (data.cellName == "A1" || data.cellName == "A2") {
          titleCell.font = {
            size: 18,
          };
        } else {
          titleCell.font = {
            size: 16,
          };
        }
        if (data.cellName == "A1") {
          titleCell.border = {
            bottom: {
              style: "medium",
              color: {
                argb: "00FFFFFF",
              }
            }
          }
        }
        titleCell.value = data.value;
        titleCell.alignment = data.style;
      },
      // 设置单元格属性
      setRowStyle(row, sheet) {
        sheet.getRow(row).eachCell((cell, rowIdx) => {
          cell.border = {
            top: {
              style: 'medium',
            },
            left: {
              style: 'medium',
            },
            bottom: {
              style: 'medium',
            },
            right: {
              style: 'medium',
            }
          }
          if (row == 12 + this.listData.length) {
            cell.font = {
              size: 14,
              bold: rowIdx == 6 || rowIdx == 7 ? false : true
            }
          } else {
            cell.font = {
              size: 14,
              bold: rowIdx == 1 ? true : false
            }
          }
          
          cell.alignment  = {
            vertical: "middle",
            horizontal: "center",
          }
        });
      },
      // 设置底部单元格属性
      setBottomRowStyle(row, sheet) {
        sheet.getRow(row).eachCell((cell, rowIdx) => {
          cell.border = {
            top: {
              style: 'medium',
            },
            left: {
              style: 'medium',
            },
            bottom: {
              style: 'medium',
            },
            right: {
              style: 'medium',
            }
          }
          cell.font = {
            size: 14,
            bold: rowIdx == 1 || rowIdx == 2 ? true : false
          }
          if (rowIdx == 1 || rowIdx == 2) {
            cell.alignment  = {
              vertical: "middle",
              horizontal: "right",
            }
          }else {
            cell.alignment  = {
              vertical: "middle",
              horizontal: "left",
            }
          }
        });
      }
    }
  }
</script>

<style >
  .table {
    width: calc(100% - 40px);
    padding: 20px;
  }

  .table table {
    width: 100%;
    table-layout: fixed;
    border-color: #eeeeee;
    border-spacing: 0;
    border-collapse: collapse;
  }

  table caption {
    color: #222222;
    font-size: 24px;
  }

  table tr td {
    height: 30px;
  }

  .exportBtn {
    margin: 20px;
    height: 40px;
    width: 100px;
    line-height: 40px;
    text-align: center;
    font-size: 16px;
    background-color: #4169e1;
    color: white;
    border-radius: 4px;
    cursor: pointer;
  }
</style>
